<template>
  <div>
    <div
      v-if="value === 'Rain'"
      class="icon sun-shower"
    >
      <div class="cloud"></div>
      <div
        :class="[day ? 'sun' : 'moon']"
      >
        <div :class="[day ? 'rays' : '']"></div>
      </div>
      <div class="rain"></div>
    </div>

    <div
      v-if="value === 'Thunderstorm'"
      class="icon thunder-storm"
    >
      <div class="cloud"></div>
      <div class="lightning">
        <div class="bolt"></div>
        <div class="bolt"></div>
      </div>
    </div>

    <div
      v-if="value === 'Clouds'"
      class="icon cloudy"
    >
      <div class="cloud"></div>
      <div class="cloud"></div>
    </div>

    <div
      v-if="value === 'Snow'"
      class="icon flurries"
    >
      <div class="cloud"></div>
      <div class="snow">
        <div class="flake"></div>
        <div class="flake"></div>
      </div>
    </div>

    <div
      v-if="value === 'Clear'"
      class="icon sunny"
    >
      <div
        :class="[ day ? 'sun' : 'moon']"
      >
        <div :class="[day ? 'rays' : '']"></div>
      </div>
    </div>

    <div
      v-if="value === 'Drizzle'"
      class="icon rainy"
    >
      <div class="cloud"></div>
      <div class="rain"></div>
    </div>

    <div
      v-if="mistWeather.includes(value)"
      class="mist"
    >
      <div class="fog"></div>
      <div class="fog"></div>
      <div class="fog"></div>
      <div class="fog"></div>
      <div class="fog"></div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
    name: 'IconWeather',
    props: {
      value: {
        type: String,
        default: 'Clouds',
        validator (val) {
          return ['Rain', 'Thunderstorm', 'Clouds', 'Snow', 'Clear', 'Drizzle', 'Mist'].includes(val);
        }
      },
      day: {
        type: Boolean,
        default: true
      }
    },
    setup () {
      const mistWeather = ref(['Mist', 'Smoke', 'Haze', 'Dust', 'Fog', 'Sand', 'Dust', 'Ash', 'Squall', 'Tornado'])

      return {
        mistWeather
      }
    }
}
</script>

<style scoped>
.icon {
  position: relative;
  display: inline-block;
  width: 8em;
  height: 6em;
  font-size: 1em;
}

.cloud {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 3.6875em;
  height: 3.6875em;
  margin: -1.84375em;
  background: currentColor;
  border-radius: 50%;
  color: #d5e2f1;
  box-shadow:
      -2.1875em 0.6875em 0 -0.6875em,
      2.0625em 0.9375em 0 -0.9375em,
      0 0 0 0.375em #d5e2f1,
      -2.1875em 0.6875em 0 -0.3125em #d5e2f1,
      2.0625em 0.9375em 0 -0.5625em #d5e2f1;
}

.cloud:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: -0.5em;
  display: block;
  width: 4.5625em;
  height: 1em;
  background: currentColor;
  box-shadow: 0 0.4375em 0 -0.0625em #d5e2f1;
}

.cloud:nth-child(2) {
  z-index: 0;
  background: #92b7e0;
  box-shadow:
      -2.1875em 0.6875em 0 -0.6875em #92b7e0,
      2.0625em 0.9375em 0 -0.9375em #92b7e0,
      0 0 0 0.375em #92b7e0,
      -2.1875em 0.6875em 0 -0.3125em #92b7e0,
      2.0625em 0.9375em 0 -0.5625em #92b7e0;
  opacity: 0.3;
  transform: scale(0.5) translate(6em, -3em);
  animation: cloud 4s linear infinite;
}

.cloud:nth-child(2):after {
  background: #92b7e0;
}

.sun {
  animation: spin 12s infinite linear;
  background-color: #ffe600;
  border-radius: 50%;
  box-shadow: 0 0 0 0.375em #ffd000;
  height: 2.5em;
  left: 50%;
  margin: -1.25em;
  position: absolute;
  top: 50%;
  width: 2.5em;
}

.moon {
  background-color: rgb(247, 247, 197);
  border-radius: 50%;
  box-shadow: 0 0 0.775em rgb(238, 238, 137);
  height: 2.5em;
  left: 50%;
  margin: -1.25em;
  position: absolute;
  top: 40%;
  width: 2.5em;
  animation: moonlight 6s infinite linear;
}

.cloud+.moon {
  margin: -2em 1em;
}

.rays {
  position: absolute;
  top: -2em;
  left: 50%;
  display: block;
  width: 0.375em;
  height: 1.125em;
  margin-left: -0.1875em;
  background: #ffd000;
  border-radius: 0.25em;
  box-shadow: 0 5.375em #ffd000;
}

.rays:before,
.rays:after {
  content: '';
  position: absolute;
  top: 0em;
  left: 0em;
  display: block;
  width: 0.375em;
  height: 1.125em;
  transform: rotate(60deg);
  transform-origin: 50% 3.25em;
  background: #ffd000;
  border-radius: 0.25em;
  box-shadow: 0 5.375em #ffd000;
}

.rays:before {
  transform: rotate(120deg);
}

.cloud+.sun {
  margin: -2em 1em;
}

.rain,
.lightning,
.snow {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 3.75em;
  height: 3.75em;
  margin: 0.375em 0 0 -2em;
  background-color: transparent;
}

.rain:after {
  content: '';
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 1.125em;
  height: 1.125em;
  margin: -1em 0 0 -0.25em;
  background: #0cf;
  border-radius: 100% 0 60% 50% / 60% 0 100% 50%;
  box-shadow:
      0.625em 0.875em 0 -0.125em rgba(255, 255, 255, 0.2),
      -0.875em 1.125em 0 -0.125em rgba(255, 255, 255, 0.2),
      -1.375em -0.125em 0 rgba(255, 255, 255, 0.2);
  transform: rotate(-28deg);
  animation: rain 3s linear infinite;
}

.bolt {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -0.25em 0 0 -0.125em;
  color: #d5e2f1;
  opacity: 0.3;
  animation: lightning 2s linear infinite;
}

.bolt:nth-child(2) {
  width: 0.5em;
  height: 0.25em;
  margin: -1.75em 0 0 -1.875em;
  transform: translate(2.5em, 2.25em);
  opacity: 0.2;
  animation: lightning 1.5s linear infinite;
}

.bolt:before,
.bolt:after {
  content: '';
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  margin: -1.625em 0 0 -1.0125em;
  border-top: 1.25em solid transparent;
  border-right: 0.75em solid;
  border-bottom: 0.75em solid;
  border-left: 0.5em solid transparent;
  transform: skewX(-10deg);
}

.bolt:after {
  margin: -0.25em 0 0 -0.25em;
  border-top: 0.75em solid;
  border-right: 0.5em solid transparent;
  border-bottom: 1.25em solid transparent;
  border-left: 0.75em solid;
  transform: skewX(-10deg);
}

.bolt:nth-child(2):before {
  margin: -0.75em 0 0 -0.5em;
  border-top: 0.625em solid transparent;
  border-right: 0.375em solid;
  border-bottom: 0.375em solid;
  border-left: 0.25em solid transparent;
}

.bolt:nth-child(2):after {
  margin: -0.125em 0 0 -0.125em;
  border-top: 0.375em solid;
  border-right: 0.25em solid transparent;
  border-bottom: 0.625em solid transparent;
  border-left: 0.375em solid;
}

.flake:before,
.flake:after {
  animation: spin 8s linear infinite reverse;
  color: #a0c8f6;
  content: '\2744';
  left: 50%;
  line-height: 1em;
  margin: -1.025em 0 0 -1.0125em;
  opacity: 0.2;
  position: absolute;
  top: 50%;
}

.flake:after {
  margin: 0.125em 0 0 -1em;
  font-size: 1.5em;
  opacity: 0.4;
  animation: spin 14s linear infinite;
}

.flake:nth-child(2):before {
  margin: -0.5em 0 0 0.25em;
  font-size: 1.25em;
  opacity: 0.2;
  animation: spin 10s linear infinite;
}

.flake:nth-child(2):after {
  margin: 0.375em 0 0 0.125em;
  font-size: 2em;
  opacity: 0.4;
  animation: spin 16s linear infinite reverse;
}

.mist {
  height: 4em;
  position: relative;
  width: 4em;
}

.fog:nth-child(1) {
  background-color: #48484a;
  height: 4px;
  position: relative;
  left: 65%;
  top: 20%;
  width: 1em;
  border-radius: 8px;
  animation: move1 4s ease-in-out infinite;
}

.fog:nth-child(2) {
  background-color: #48484a;
  height: 4px;
  position: relative;
  left: 40%;
  width: 2.5em;
  top: 30%;
  border-radius: 8px;
  animation: move2 4s ease-in-out infinite;
}

.fog:nth-child(3) {
  background-color: #48484a;
  height: 4px;
  position: relative;
  left: 60%;
  top: 40%;
  width: 3em;
  border-radius: 8px;
  animation: move3 4s ease-in-out infinite;
}

.fog:nth-child(4) {
  background-color: #48484a;
  height: 4px;
  position: relative;
  left: 40%;
  width: 3em;
  top: 50%;
  border-radius: 8px;
  animation: move4 4s ease-in-out infinite;
}

.fog:nth-child(5) {
  background-color: #48484a;
  height: 4px;
  position: relative;
  left: 60%;
  width: 2em;
  top: 60%;
  border-radius: 8px;
  animation: move5 4s ease-in-out infinite;
}

/* Animations */

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes cloud {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 0;
    transform: scale(0.5) translate(-200%, -3em);
  }
}

@keyframes rain {
  0% {
    background: #0cf;
    box-shadow:
        0.625em 0.875em 0 -0.125em rgba(255, 255, 255, 0.2),
        -0.875em 1.125em 0 -0.125em rgba(255, 255, 255, 0.2),
        -1.375em -0.125em 0 #0cf;
  }

  25% {
    box-shadow:
        0.625em 0.875em 0 -0.125em rgba(255, 255, 255, 0.2),
        -0.875em 1.125em 0 -0.125em #0cf,
        -1.375em -0.125em 0 rgba(255, 255, 255, 0.2);
  }

  50% {
    background: rgba(255, 255, 255, 0.3);
    box-shadow:
        0.625em 0.875em 0 -0.125em #0cf,
        -0.875em 1.125em 0 -0.125em rgba(255, 255, 255, 0.2),
        -1.375em -0.125em 0 rgba(255, 255, 255, 0.2);
  }

  100% {
    box-shadow:
        0.625em 0.875em 0 -0.125em rgba(255, 255, 255, 0.2),
        -0.875em 1.125em 0 -0.125em rgba(255, 255, 255, 0.2),
        -1.375em -0.125em 0 #0cf;
  }
}

@keyframes lightning {
  45% {
    color: #d5e2f1;
    background: #d5e2f1;
    opacity: 0.2;
  }

  50% {
    color: yellow;
    background: #0cf;
    opacity: 1;
  }

  55% {
    color: #d5e2f1;
    background: #d5e2f1;
    opacity: 0.2;
  }
}


@keyframes moonlight {
  0% {
    box-shadow: 0 0 0.175em rgb(238, 238, 137);
  }
  50% {
    box-shadow: 0 0 0.975em rgb(238, 238, 137);
  }
  100% {
    box-shadow: 0 0 0.175em rgb(238, 238, 137);
  }
}

@keyframes move1 {
  0% {
    left: 40%;
  }
  50% {
    left: 65%;
  }
  100% {
    left: 40%;
  }
}

@keyframes move2 {
  0% {
    left: 30%;
  }
  50% {
    left: 40%;
  }
  100% {
    left: 30%;
  }
}

@keyframes move3 {
  0% {
    left: 50%;
  }
  50% {
    left: 40%;
  }
  100% {
    left: 50%;
  }
}

@keyframes move4 {
  0% {
    left: 20%;
  }
  50% {
    left: 40%;
  }
  100% {
    left: 20%;
  }
}

@keyframes move5 {
  0% {
    left: 50%;
  }
  50% {
    left: 60%;
  }
  100% {
    left: 50%;
  }
}

</style>